* {
    margin: 0;
    padding:0;
    box-sizing: border-box;
}

.main-container {
    width:100%;
    background-color:#ececec;
}

.container-bot {
    max-width:1200px;
    margin:auto;
    border-top: #888 1px solid;
    padding-top: 8px;
    margin-top: 20px;
}

.unselectable {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.green {
    color: green;
}